import { HttpClient } from "@angular/common/http";
import { Inject, Injectable, Injector } from "@angular/core";
import { Observable } from "rxjs";
import { FORM_PATH_TOKEN, FormManifest, IFormManifestService } from "@farris/devkit";
import { share } from "rxjs/operators";

@Injectable()
export class FormManifestService implements IFormManifestService {
  private static mainfests = new Map<string, Observable<FormManifest>>();
  constructor(
    private injector: Injector,
    @Inject(FORM_PATH_TOKEN) private modulePath,
    private httpClient: HttpClient
  ) { }
  public load(): Observable<FormManifest> {
    if (this.modulePath.endsWith('\/')) {
      this.modulePath = this.modulePath.substring(0, this.modulePath.length - 1);
    }
    const formManifestPath = `${this.modulePath}/expressions/form.manifest.json?version=${new Date().valueOf().toString()}`;
    const request$ = FormManifestService.mainfests.get(this.modulePath);
    if (request$) {
      return request$;
    } else {
      const req$ = this.httpClient.get<FormManifest>(formManifestPath, { responseType: 'json' }).pipe(share());
      FormManifestService.mainfests.set(this.modulePath, req$);
      return req$;
    }
  }
}
