import { Injectable, ComponentFactoryResolver, ReflectiveInjector, Provider, ComponentRef, InjectFlags, NgModuleFactory, Injector, Optional } from '@angular/core';
import { BsModalService, ModalOptions } from '@farris/ui-modal';
