  /**
   * ä»¥å¼¹æ¡ãä¾§è¾¹æ ææ°æ ç­¾é¡µæ¹å¼æå¼è¡¨å
   * @param mode æå¼æ¹å¼ï¼æ¯æ`modal`å¼¹çªã`sidebar`ä¾§è¾¹æ ã`tab`æ°æ ç­¾é¡µ
   * @param modalId å¼¹çªidï¼å¦æmode=`modal`ä¸æ²¡æurlï¼
   * @param configs å¼¹çªéç½®
   * @param url è¿ç«¯è¡¨åurl
   * @param tabId æ ç­¾é¡µidï¼modal=tabæ¶å¿å¡«
   * @param tabType æ ç­¾é¡µç±»åï¼`menu` æ`app`
   * @param funcOrAppId èåæåºç¨id
   * @param appEntrance åºç¨å¥å£
   * @param params åæ°
   * @param tabName æ°æ ç­¾é¡µåç§°
   * @param enableRefresh å¯ç¨å¯å¨å·æ°
   * @param destructuring æ¯å¦è§£æ
   */
  public open(mode: 'modal' | 'sidebar' | 'tab', modalId?: string, url?: string, configs?: any, tabId?: string, tabType?: 'menu' | 'app', funcOrAppId?: string, appEntrance?: string, params?: any, tabName?: string, enableRefresh?: any, destructuring?: any) {
    const pageModalService = this.injector.get<FEPageModalService>(FEPageModalService, null);
    if (!pageModalService) {
      throw new Error('get FEPageModalService failed.');
    }
    // æ ¡éªåæ°æ¯å¦åæ³
    if (!mode) {
      throw new Error('[NavigationService]->open,modeåæ°ä¸è½ä¸ºç©ºï¼');
    }
    if (mode === 'modal' || mode === 'sidebar') {
      if (!modalId && !url) {
        throw new Error('å¼¹çªåä¾§è¾¹æ æ¨¡å¼æ¶å¼¹çªå®¹å¨idåè¡¨åè·¯å¾ä¸è½åæ¶ä¸ºç©ºï¼');
      }
      if (modalId && url) {
        throw new Error('å¼¹çªåä¾§è¾¹æ æ¨¡å¼æ¶å¼¹çªå®¹å¨idåè¡¨åè·¯å¾ä¸è½åæ¶å­å¨ï¼');
      }
      const uiStateConfig = this.getObjectTypeConfig(params);
      const modalConfig = this.buildConfigs(configs);
      if (mode === 'sidebar') {
        modalConfig.dialogType = mode;
      }
      let pageModalRef = null;
      if (modalId) {
        const farrisFormService = this.injector.get<FarrisFormService>(FarrisFormService, null);
        if (!farrisFormService) {
          return;
        }
        let componentType = farrisFormService.getControls(modalId);
        const componentRef = this.createComponentRef(componentType, uiStateConfig);
        pageModalRef = pageModalService.show(componentRef, modalConfig);
      } else if (url) {
        pageModalRef = pageModalService.showByUrl(url, modalConfig);
      }
      if (pageModalRef && !!pageModalRef.content) {
        pageModalRef.content.isDialogRootComponent = true;
        pageModalRef.content.dialogRef = pageModalRef;
        const header = pageModalRef.dialog && pageModalRef.dialog.instance && pageModalRef.dialog.instance.el && pageModalRef.dialog.instance.el.nativeElement && pageModalRef.dialog.instance.el.nativeElement.querySelector(".f-page-header");
        if (header && pageModalRef.dialog.instance.draggbar) {
          pageModalRef.dialog.instance.draggbar.handle = header;
          header.style.cursor = 'move';
        }
      }
    } else if (mode === 'tab') {
      if (!tabId || !tabType || !funcOrAppId) {
        if (this.formNotifyService) {
          this.formNotifyService.warning(this.languageService.appOrFuncIdRequired, { hideTitle: true });
        }
        throw new Error('æ°æ ç­¾æ¨¡å¼æ¶æ ç­¾é¡µidãæ ç­¾ç±»åãèåæåºç¨idåä¸è½ä¸ºç©ºï¼');
      }
      if (tabType === 'app' && !appEntrance) {
        throw new Error('ä»¥åºç¨æ¹å¼æå¼æ¶å¥å£åºç¨ä¸è½ä¸ºç©ºï¼');
      }
      if (tabType == 'app') {
        this.openApp(tabId, funcOrAppId, appEntrance, params, false, tabName, enableRefresh, destructuring);
      } else if (tabType === 'menu') {
        this.openMenu(tabId, funcOrAppId, params, false, enableRefresh, tabName, destructuring);
      }
    } else {
      throw new Error('ä¸æ¯æçæ¨¡å¼ï¼');
    }
    // this.pageModalService.
  }
