  /**
   * æ¸ç©ºå¸®å©å¾éï¼åªè½æè½½å°å¸®å©åäºä»¶ï¼
   */
  public clearHelpSelections() {
    const self: any = this;
    const helpElement = self.context && self.context.eventParam && self.context.eventParam.instance || null;
    if (helpElement) {
      helpElement.displayValue = '';
    }
  }
  /**
   * å¾éå½åè¡
   * @param frameId frameId
   * @param mapFields å­æ®µæ å°
   * @param primaryKeyValue å¸®å©ä¸»é®å­æ®µ
   */
  public checkCurrentRow(frameId: string, mapFields: string, primaryKeyValue: string) {
    const self: any = this;
    const helpElement = self.context && self.context.eventParam && self.context.eventParam.instance || null;
    if (!primaryKeyValue) {
      primaryKeyValue = 'id';
    }
    const mappings = JSON.parse(mapFields);
    if (helpElement) {
      frameId = frameId || this.viewModel.frameContext.frameId;
      if (frameId) {
        const frameContext = this.getFrameContextById(frameId);
        if (frameContext) {
          const bindingPath = frameContext.viewModel.bindingPath;
          if (bindingPath) {
            const bindingPaths = bindingPath.split('/').filter(p => p);
            const bindingList = frameContext.bindingData.getValue(bindingPaths) as BindingList;
            const currentRow = bindingList.currentItem;
            const path = mappings[primaryKeyValue];
            if (path) {
              const value = this.getValueByPath(currentRow, path);
              helpElement.displayValue = value;
            }
          }
        }
      }
    }
  }
  /**
   * ä»¥å¸®å©å¾éæ°æ®ä½ä¸ºä¾æ®ï¼æ¹éæ°å¢ä¸»è¡¨æ°æ®
   * @param frameId ä¸»è¡¨æå¨frameçid
   * @param mapFields å¸®å©å­æ®µä¸ä¸»è¡¨å­æ®µæ å°
   * @description ä»¥å¸®å©å¾éæ°æ®ä½ä¸ºä¾æ®ï¼æ¹éæ°å¢ä¸»è¡¨æ°æ®
   */
  public batchAppend(frameId: string, mapFields: string) {
    const self: any = this;
    const result = self.context && self.context.eventParam || [];
    if (!mapFields) {
      throw new Error('mapFields can`t be empty.');
    }
    if (result && Array.isArray(result) && result.length > 0) {
      const mappings = JSON.parse(mapFields);
      // ä¸æ¯bindingPathåæframeIdå¤ç
      let bindingPath = '/';
      const appContext = this.injector.get<AppContext>(AppContext, null);
      if (appContext) {
        const frameContext = appContext.frameContextManager.getFrameContextById(frameId);
        if (frameContext) {
          bindingPath = frameContext.viewModel.bindingPath || '/';
        } else {
          throw new Error('frameId is invalid!');
        }
      }
      const defaultValues = [];
      result.forEach((item) => {
        const defaultValue = {};
        Object.keys(mappings).forEach((prop: string) => {
          const value = this.getValueByPath(item, prop);
          const fields = mappings[prop];
          const groups: any[] = fields.split(',').filter(p => p);
          groups.forEach(group => {
            const paths = group.split('.').filter(p => p);
            this.setValueByPath(defaultValue, paths.join('.'), value);
          });
        });
        defaultValues.push(defaultValue);
      });
      this.formLoadingService.show();
      return this.repository.batchAppend(defaultValues).pipe(
        tap(() => this.formLoadingService.hide()),
        map(() => true)
      );
    }
    return of(true);
  }
  /**
   * ä»¥å¸®å©å¾éæ°æ®ä½ä¸ºä¾æ®ï¼æ¹éæ°å¢ä»è¡¨/ä»ä»è¡¨
   * @param frameId ä»è¡¨/ä»ä»è¡¨æå¨frameçid
   * @param mapFields å¸®å©å­æ®µä¸ä»è¡¨/ä»ä»è¡¨å­æ®µæ å°
   * @description ä»¥å¸®å©å¾éæ°æ®ä½ä¸ºä¾æ®ï¼æ¹éæ°å¢ä»è¡¨/ä»ä»è¡¨
   */
