export class ExceptionStrategy {
  constructor(
    public messagerService: MessagerService,
    @Optional() public languageService: LanguageService) {
  }
}

/**
 * ä¿¡æ¯çº§å«å¼å¸¸å¤ç
 */
export class InfoExceptionStrategy extends ExceptionStrategy implements IExceptionStrategy {
  constructor(
    messagerService: MessagerService,
    @Optional() languageService: LanguageService) {
    super(messagerService, languageService);
  }

  public handleException(error: ServerExceptionEntity): void {
    if (!error) {
      return;
    }

    this.messagerService.info(error.Message);
  }
}


