import { IQueryConditionHandler } from "./iquerycondition.handler";
import { QueryCondition, DropDownListValue } from "@farris/component-querycondition";
import { CompareType } from '../enum/compare-type.enum';
import { ValueType } from '../enum/value-type.enum';
import { RelationType } from '../enum/relation-type.enum';

export class DropDownHandler implements IQueryConditionHandler{
    convert(qc: QueryCondition) {
        const ddv = (qc.value as DropDownListValue);
        const arr = [];
        ddv.value.forEach(dv => {
            arr.push({
                'FilterField' : qc.fieldCode,
                'Compare' : CompareType.Equal,
                'Value' : dv.value,
                'Relation' : RelationType.Or,
                'Expresstype':ValueType.Value
            });
        });
        arr[0]['Lbracket'] = '(';
        arr[arr.length -1]['Rbracket'] = ')';
        arr[arr.length -1]['Relation'] = RelationType.And;
        return arr;
    }
}